<template>
  <div
    class="ywb-inline-alert"
    :class="{
      'is-error': type === 'error',
      'is-info': type === 'info',
      'is-warning': type === 'warning',
      'is-success': type === 'success'
    }"
  >
    <span class="icon">
      <template v-if="type === 'success'">
        <feather-check-circle-icon />
      </template>
      <template v-if="type !== 'success'">
        <feather-alert-circle-icon />
      </template>
    </span>
    <span class="text">{{ text }}</span>
  </div>
</template>

<script>
export default {
  name: "InlineAlert",
  props: {
    type: {
      type: String,
      default: "error",
      validator: function(value) {
        return ["error", "success", "info", "warning"].includes(value);
      }
    },
    text: {
      type: String,
      required: true
    }
  }
};
</script>

