  display: flex;
  padding: 12px 32px;
  border-radius: 6px;
  align-items: center;
  margin-bottom: 8px;
}
.ywb-inline-alert .icon {
  margin-right: 12px;
}
.ywb-inline-alert .text {
  font-size: 0.95rem;
  color: #858585;
}
.ywb-inline-alert.is-error {
  background-color: #f9f3f3;
}
.ywb-inline-alert.is-error .icon {
