}
.ywb-inline-alert.is-warning {
  background-color: #ffede4;
}

.ywb-inline-alert.is-warning .icon {
  color: #ffa800;
}

.ywb-inline-alert.is-info {
  background-color: #d4dfff;
}

.ywb-inline-alert.is-info .icon {
  color: #6993ff;
}

.ywb-inline-alert.is-success {
  background-color: #d4ffef;
}

