<template>
  <Modal
    :title="title"
    :opened="show"
    is-screen
    :on-close="onClose"
    hide-action-buttons
  >
    <div class="inline-alerts-wrapper" v-if="errors.length > 0">
      <InlineAlert
        v-for="(error, index) in errors"
        :key="index"
        :text="error"
      />
      <Separator :size="16" />
    </div>
    <transition name="fade">
      <div class="signin-box" v-if="type === 'signin'">
        <Input
          label="E-mail"
          v-model="email"
          :has-error="errorTargets.includes('email')"
        />
        <Separator :size="12" />
        <Input
          label="Mot de passe"
          v-model="password"
          :has-error="errorTargets.includes('password')"
        />
        <Separator :size="4" />
        <small class="link text-right block">
          <a href="javascript:void(0)">Mot de passe oublié ?</a>
        </small>
        <Separator :size="24" />
        <Button @click="onSubmit" :text="submitButtonText" full />
        <Separator :size="8" />
        <small class="block text-left">
          Vous n'avez pas de compte ?
          <a
            href="javascript:void(0)"
            class="text-underline text-black text-semibold"
            @click="type = 'signup'"
          >
            Inscription
          </a>
        </small>
      </div>
      <div class="signup-box" v-if="type === 'signup'">
        <Input
          label="Nom"
          v-model="lastname"
          :has-error="errorTargets.includes('lastname')"
        />
        <Separator :size="12" />
        <Input
          label="Prénom"
          v-model="firstname"
          :has-error="errorTargets.includes('firstname')"
        />
        <Separator :size="12" />
        <Input
          label="E-mail"
          v-model="email"
          :has-error="errorTargets.includes('email')"
        />
        <Separator :size="12" />
        <Input
          label="Mot de passe"
          v-model="password"
          :has-error="errorTargets.includes('password')"
        />
        <Separator :size="24" />
        <Button @click="onSubmit" :text="submitButtonText" full />
        <Separator :size="8" />
        <small class="block text-left">
          Déjà un compte ?
          <a
            href="javascript:void(0)"
            class="text-underline text-black text-semibold"
            @click="type = 'signin'"
          >
            Connexion
          </a>
        </small>
      </div>
    </transition>
  </Modal>
</template>

<script>
import Modal from './Modal';
import Input from './Input';
import Separator from './Separator';
import Button from './Button';
import InlineAlert from './InlineAlert';

export default {
  name: 'SignScreen',
  components: {Modal, Input, Separator, Button, InlineAlert},
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    errors: {
      type: Array,
      default: () => [],
    },
    errorTargets: {
      type: Array,
      default: () => [],
    },
  },
  data: function() {
    return {
      type: 'signin',
      email: null,
      password: null,
      firstname: null,
      lastname: null,
    };
  },
  watch: {
    email: function(value) {
      console.log('email', value);
    },
    type: function() {
      this.$emit('type-change');
    },
    errorTargets: function(value) {
      console.log('w', value);
    },
  },
  computed: {
    title: function() {
      if (this.type === 'signin') {
        return 'Connexion';
      } else {
        return 'Inscription';
      }
    },
    submitButtonText: function() {
      if (this.type === 'signin') {
        return 'Se connecter';
      } else {
        return "S'incrire";
      }
    },
  },
  methods: {
    onSubmit: function() {
      this.$emit('submit', {
        email: this.email,
        password: this.password,
        firstname: this.firstname,
        lastname: this.lastname,
        type: this.type,
      });
    },
    onClose() {
      console.log('close');
      this.lastname = '';
      this.firstname = '';
      this.email = '';
      this.password = '';
      this.$emit('close');
    },
  },
};
</script>

<style scoped>
.ywb-sign-screen {
}
</style>
