<template>
  <div
    class="ywb-separator"
    :style="calcStyle"
    :class="{ 'has-border': hasBorder }"
  ></div>
</template>

<script>
export default {
  name: "Separator",
  props: {
    size: {
      type: Number,
      default: 16,
      validator: function(value) {
        return value >= 0;
      }
    },
    bottomSize: {
      type: Number,
      default: 0,
      validator: function(value) {
        return value >= 0;
      }
    },
    hasBorder: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    calcStyle: function() {
      return `height: ${this.$props.size}px; margin-bottom: ${this.$props.bottomSize}px;`;
    }
  }
};
</script>

<style scoped>
.ywb-separator {
  display: block;
}
.ywb-separator.has-border {
  border-bottom: 1px solid #e4e8ed;
}
</style>
