<template>
  <button
    class="ywb-button"
    :class="{
      'is-primary': type === 'primary',
      'is-secondary': type === 'secondary',
      'is-blank': type === 'blank',
      'is-black': type === 'black',
      'is-full': full,
      'is-small': small,
      'is-disabled': disabled
    }"
    @click="onClick"
  >
    {{ text }}
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    text: String,
    type: {
      type: String,
      default: "default",
      validator: function(value) {
        return ["primary", "secondary", "black", "blank", "default"].includes(
          value
        );
      }
    },
    full: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onClick: function(e) {
      if (this.$props.disabled) return;
      this.$emit("click", e);
    }
  }
};
</script>

<style scoped>
.ywb-button {
  padding: 8px 22px;
  display: inline-block;
  background-color: #6993ff;
  color: #ffffff;
  font-weight: 500;
  border-radius: 6px;
  transition: 200ms all linear;
  opacity: 1;
}
.ywb-button.is-small {
  font-size: 0.9rem;
  padding: 6px 18px;
}
.ywb-button.is-full {
  width: 100%;
}
.ywb-button:hover {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
}
.ywb-button:active {
  opacity: 0.5;
}
.ywb-button.is-primary {
  background-color: #d7f9ef;
  color: #0abb87;
}
.ywb-button.is-secondary {
  background-color: #fff4de;
  color: #ffa800;
}
.ywb-button.is-blank {
  background-color: transparent;
  text-decoration: underline;
  color: #212121;
}

.ywb-button.is-black {
  background-color: #212121;
  color: #ffffff;
}
.ywb-button:focus {
  outline: none;
}
.ywb-button.is-disabled {
  opacity: 0.4;
  cursor: not-allowed;
  pointer-events: none;
}
</style>
