<template>
  <div class="input-group-wrapper" :class="{ 'has-error': hasError }">
    <label v-if="label" :for="id">{{ label }}</label>
    <div
      class="ywb-input-wrapper"
      :style="{ backgroundColor: backgroundColor }"
    >
      <span class="input-addon"><slot name="left-addon"></slot></span>
      <input
        :value="content"
        @input="onInput"
        :type="type"
        :placeholder="placeholder"
        @focus="onFocus"
        @blur="onBlur"
        @change="$emit('change', $event)"
        :style="{ backgroundColor: backgroundColor }"
        :id="id"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "Input",
  props: {
    type: {
      type: String,
      default: "text",
      validator: function(value) {
        return ["text", "number"].indexOf(value) !== -1;
      }
    },
    placeholder: String,
    value: String,
    backgroundColor: String,
    label: String,
    hasError: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: null,
      content: this.$props.value
    };
  },
  methods: {
    onFocus: function(event) {
      this.$emit("c-focus", event);
    },
    onBlur: function(event) {
      this.$emit("c-blur", event);
    },
    onInput(e) {
      this.content = e.target.value;
      this.$emit("input", this.content);
    }
  },
  mounted() {
    this.id = this._uid;
  }
};
</script>

