<template>
  <div id="app">
    <SignScreen :show="show" />
  </div>
</template>

<script>
import SignScreen from './components/SignScreen.vue';


export default {
  name: 'App',
  components: {
    SignScreen
  },
  data(){
    return {
      show:false
    }
  },
  mounted() {
    //const event = new Event('loadModalComponent');

    // Listen for the event.
    document.addEventListener(
      'loadModalComponent',
      () => {
         this.show = !this.show;
      },
      false,
    );

    // Dispatch the event.
    //document.dispatchEvent(event);
  },
};
</script>

