<template>
  <div
    class="ywb-modal"
    :class="{
      'modal--open': isOpened,
      'modal--close': !isOpened,
      'is-mounted': mounted,
      'is-screen': isScreen,
    }"
  >
    <div class="modal-overlay" @click="onCloseAction(true)"></div>
    <div class="modal-content">
      <span @click="onCloseAction" class="close-button"
        ><feather-x-icon size="20"
      /></span>
      <h2 class="modal-title">
        {{ title }}
      </h2>
      <div class="modal-body"><slot></slot></div>
      <div class="modal-actions" v-if="!hideActionButtons">
        <button
          v-if="showCancelAction"
          type="button"
          class="cancel-button"
          @click="onCancelAction"
        >
          {{ cancelButtonText }}
        </button>
        <Button
          :text="primaryButtonText"
          @click="onPrimaryAction"
          :is-primary="primaryActionHasPrimaryStyle"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Button from './Button';

export default {
  name: 'Modal',
  components: {
    Button,
  },
  data: function() {
    return {
      isOpened: this.$props.opened,
      mounted: false,
    };
  },
  props: {
    opened: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
    },
    cancelButtonText: {
      type: String,
      default: 'Annuler',
    },
    primaryButtonText: {
      type: String,
      default: 'Appliquer',
    },
    onCancel: Function,
    onPrimary: Function,
    onClose: Function,
    closeOnOverlay: {
      type: Boolean,
      default: true,
    },
    primaryActionHasPrimaryStyle: {
      type: Boolean,
      default: false,
    },
    showCancelAction: {
      type: Boolean,
      default: true,
    },
    isScreen: {
      type: Boolean,
      default: false,
    },
    hideActionButtons: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    opened: function(newVal) {
      this.isOpened = newVal;
    },
  },
  mounted() {
    const vm = this;
    setTimeout(function() {
      vm.mounted = true;
    }, 500);
  },
  methods: {
    onCancelAction: function() {
      if (this.$props.onCancel) {
        if (this.$props.onCancel()) {
          this.isOpened = false;
        }
      }
      this.isOpened = false;
    },
    onPrimaryAction: function() {
      if (this.$props.onPrimary) {
        if (this.$props.onPrimary()) {
          this.isOpened = false;
        }
      }
    },
    onCloseAction: function(isFromOverlay) {
      if (isFromOverlay) {
        if (this.$props.closeOnOverlay) {
          this.isOpened = false;
          return this.$props.onClose && this.$props.onClose();
        }
      } else {
        this.isOpened = false;
        return this.$props.onClose && this.$props.onClose();
      }
    },
  },
};
</script>

